import { ErrorMessage } from "@hookform/error-message";
import React, { useMemo, useRef } from "react";
import { useForm } from "react-hook-form";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import styled from "styled-components";
import useRedirect from "../../common/hooks/useRedirect";
import { signupAction } from "../../redux/action/userAction";

const SignUp = ({ className }) => {
  const auth = useSelector((state) => state.userStatus.auth, shallowEqual);
  const role = useSelector((state) => state.userStatus?.userInfo?.user?.role, shallowEqual);
  const error = useSelector((state) => state.error?.user_signup, shallowEqual);
  useRedirect(
    useMemo(
      () => [
        { condition: role >= 1, to: "/agency-dashboard" },
        { condition: auth, to: "/user" },
      ],
      [role, auth]
    )
  );
  const { register, handleSubmit, errors, watch } = useForm();
  const history = useHistory();
  const password = useRef({});
  password.current = watch("password", "");
  const dispatch = useDispatch();
  const handle = ({ rePassword, ...data }) => {
    dispatch(
      signupAction(data, (role) => {
        if (role === 1) history.push("/agency-dashboard");
        if (role === 0) history.push("/getting-report");
        else history.push("/");
      })
    );
  };
  return (
    <main className={`${className} form d-flex align-items-center flex-grow-1`}>
      <section className="form__form">
        <p className="fs__title--main fw-bold">Đăng ký Ngay</p>
        <p className="fs__text">Lorem ipsum dolor sit amet consectetur adipisicing elit.</p>
        <form onSubmit={handleSubmit(handle)}>
          <input
            type="text"
            name="name"
            placeholder="Tên Đăng nhập"
            ref={register({
              required: "Bạn cần nhập tên đăng nhập",
              minLength: { value: 6, message: "Tên đăng nhập có ít nhất 6 ký tự" },
              pattern: {
                value: /^[a-zA-Z0-9]{6,}$/,
                message: "Tên đăng nhập không được chứa khoảng trống và ký tự đặc biệt",
              },
            })}
          />
          <input
            type="email"
            name="email"
            placeholder="Email"
            ref={register({ required: "Bạn cần nhập email" })}
          />
          <input
            type="password"
            name="password"
            placeholder="Mật khẩu"
            ref={register({
              required: "Bạn cần nhập mật khẩu",
              minLength: { value: 6, message: "Mật khẩu cần có ít nhất 6 ký tự" },
            })}
          />
          <input
            type="password"
            name="rePassword"
            placeholder="Nhập lại mật khẩu"
            ref={register({
              required: "Bạn cần nhập lại mật khẩu",
              validate: (value) => value === password.current || "Mật khẩu không khớp",
            })}
          />
          <input type="submit" value="Đăng ký" />
          <div className="error">{error}</div>
          {Object.keys(errors).length !== 0 && (
            <ErrorMessage errors={errors} name={Object.keys(errors)[0]} />
          )}
        </form>
      </section>
    </main>
  );
};

export default styled(SignUp)`
  width: 100%;

  .form {
    /* &__img {
      width: 60%;
      display: flex;
      background-color: rgba(0, 0, 0, 0.4);
      flex-direction: column;
      justify-content: space-between;
      padding: 3rem 100px;
      position: relative;
      &::after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        background-image: url(/images/home3-slide-1.jpg);
        background-size: cover;
      }
      p {
        max-width: 400px;
        font-weight: bold;
        font-size: 2.5em;
      }
      &__logo {
        > img {
          width: 200px;
        }
      }
      &__link {
        a {
          margin-right: 10px;
          font-weight: bolder;
        }
      }
      @media ${(props) => props.theme.breakPoints.xl} {
        width: 50%;
      }
    } */
    &__form {
      text-align: center;
      width: 100%;

      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0 40px;
      > form {
        margin: 0 auto;
        width: 400px;
        display: flex;
        flex-direction: column;
        input {
          text-indent: 20px;
          background-color: black;
          color: #fafafa;
          height: 50px;
          margin: 10px 0;
          border-radius: 8px;
          border: 1px solid #fafafa;
          font-weight: bold;
          outline: none;
          transition: border 0.2s ease-in;
          &:focus {
            border: 3px solid ${(props) => props.theme.colorSecondary};
          }
        }
        input[type="submit"] {
          background-color: ${(props) => props.theme.colorSecondary};
          color: #fafafa;
          border-width: 3px;
          border-color: ${(props) => props.theme.colorSecondary};
          &:hover {
          }
        }
        input[type="date"] {
          text-indent: 10px;
          &::-webkit-calendar-picker-indicator {
            cursor: pointer;
            filter: invert(1);
            margin-right: 10px;
          }
        }
        input::-webkit-input-placeholder {
          color: #fafafa;
        }
      }
      /* @media ${(props) => props.theme.breakPoints.xl} {
        width: 50%;
      } */
      @media ${(props) => props.theme.breakPoints.lg} {
        width: 100%;
      }
      @media ${(props) => props.theme.breakPoints.sm} {
        > form {
          width: 100%;
        }
      }
    }
  }
`;
